import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  nav_str: [{c_name:'首页',link_url:''}],
  base_place: 0,
}


export default new Vuex.Store({
  state,
  mutations: {
    c_nav_str(state, c_data) {
      state.nav_str.push(c_data)
    }
  }
})



