import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import index from '@/components/index/index'
import web from '@/components/index/web'
import python from '@/components/index/python'
import myself from '@/components/index/myself'
import search from '@/components/index/search'
import a_detail from '@/components/article_detail/a_detail'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'index',
    component: index
  }, {
    path: '/article_detail',
    name: 'article_detail',
    component: a_detail
  }, {
    path: '/web',
    name: 'web',
    component: web
  }, {
    path: '/python',
    name: 'python',
    component: python
  }, {
    path: '/myself',
    name: 'myself',
    component: myself
  }, {
    path: '/search',
    name: 'search',
    component: search
  }]
})



